<template>
  <div class="xzxc-create-menu">
    <br><el-button type="primary" @click="createClassAMenu">创建一级菜单</el-button><br><br>
    <el-dialog title="创建菜单" :visible.sync="dialogFormVisible" :width="this.$store.state.modalWidth">
      <el-form ref="ruleForm" :label-position="this.$store.state.panelLayout" :model="parend" :rules="rules">
        <el-form-item label="菜单名称:" prop="menu_name" :rules="rules_menu_name" :label-width="formLabelWidth">
          <el-input v-model="parend.menu_name" :placeholder="'请输入菜单名称'" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="菜单跳转地址:" prop="menu_href" :rules="rules_menu_href" :label-width="formLabelWidth">
          <el-input v-model="parend.menu_href" :placeholder="'菜单跳转地址,不设置无法跳转，例如：/create_menu'" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="选择菜单图标:" prop="menu_icon" :label-width="formLabelWidth">
          <el-button @click="innerVisible = true">选择图标</el-button>&nbsp;&nbsp;<el-radio :rules="rules_menu_icon" disabled v-model="parend.menu_icon" label=""><i v-if="parend.menu_icon" :class="parend.menu_icon" style="font-size:20px;color:#ff5400;"></i></el-radio>
        </el-form-item>


      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="setCreateClassAMenu()">确 定</el-button>
      </div>

      <el-dialog width="80%" :top="'100px'" title="选择图标" :visible.sync="innerVisible" append-to-body>
        <el-scrollbar :style="'height:'+winh+'px;'">
            <el-radio-group v-model="parend.menu_icon" class="iconlist">
              <el-radio v-for="(item, index) in menu_icon_arr" :key="index" :label="item"><i :class="item"></i></el-radio>
            </el-radio-group>
         </el-scrollbar>
         <div slot="footer" class="dialog-footer">
            <el-button @click="innerVisibleClose()">取 消</el-button>
            <el-button type="primary" @click="innerVisiblefn()">确 定</el-button>
          </div>
      </el-dialog>


    </el-dialog>
    <el-tree class="eltree" :data="menudata" show-checkbox node-key="id" :default-expand-all="false" :expand-on-click-node="false" :render-content="renderContent">
    </el-tree>
  </div>
</template>

<script>
import {iconarr} from './menu_icon_list.js'
// import {mapState, mapMutations} from 'vuex'
export default {
  components: {
  },
  beforeUpdate:function(to,from){
  },
  data () {
    return {
      dialogFormVisible: false,
      createMenuObj: {
        createClassAMenu: true,
        id: null
      },
      innerVisible: false,
      winh: document.documentElement.clientHeight - 300,
      formLabelWidth: '120px',
      menu_icon_arr: iconarr,
      parend: {
        menu_name: null,
        menu_href: null,
        menu_icon: null
      },
      rules_menu_name: [
        {required: true, message: '请输入菜单名称'}
      ],
      rules_menu_href: [
        {required: true, message: '请输入菜单跳转路由'}
      ],
      rules_menu_icon: [
        {required: true, message: '请输入菜单icon图标'}
      ],
      rules: {
        menu_name: [
          {required: true, message: '请输入菜单名称', trigger: 'red'}
        ],
        menu_href: [
          {required: true,message: '请输入菜单跳转路由',trigger: 'red'}
        ],
        menu_icon: [
          {required: true,message: '请输入菜单icon图标',trigger: 'red'}
        ]
      },
      menudata: [],
      menudata1: [],
      menuTreeData:{
        name:'menu_name',
        href:'menu_href',
        icon:'menu_icon',
        parentId:'cid',
        hrefP: 'menu_href'
      }
    }
  },
  props: [],
  created () {
    this.$store.commit('emotionr/emotionp/emotiona/al')
    console.log(this.$store.state.emotionr.title, '____________')
    console.log(this.$store.state.emotionr.emotionp.name, '____________')
    this.getMenuData()
  },
  computed: {
    // ...mapState({
    //   api: 'api',
    //   getCatch: 'getCatch',
    //   setCatch: 'setCatch',
    //   alertsu: 'alertsu',
    //   alerter: 'alerter',
    //   alert: 'alert',
    //   confirm: 'confirm'
    // }),
    // ...mapState('createTree',{
    //   tree1: state => state.tree1,
    //   data2treeDG: state => state.data2treeDG
    // })
  },
  methods: {
    // ...mapMutations({
    //   $axios: '$axios',
    //   $get: '$get'
    // }),
    createClassAMenu () {
      this.createMenuObj.createClassAMenu = true
      if (this.createMenuObj.createClassAMenu) {
        this.parend.id = 0
        this.createMenuObj.id = null
        this.dialogFormVisible = true
      }
      
    },
    append (data) {
      this.createMenuObj.createClassAMenu = false
      if (!this.createMenuObj.createClassAMenu) {
        this.createMenuObj.id = data.id
        this.parend.id = data.id
        this.dialogFormVisible = true
      }
    },
    deleteMenu (id) {
      var _this = this
      this.$axios({
         type: '$axios',
         obj: {
          url: _this.api.deletemenu,
          method: 'post',
          success (res) {
            if (res.status == 200) {
              _this.alertsu('删除菜单成功！')
              _this.alert({
                content: '删除菜单成功！',
                title: '提示'
              })
              _this.getMenuData()
            } else {
              _this.alertsu('删除菜单失败！')
            }
          },
          error () {
            _this.alertsu('删除菜单失败！')
          },
          data: {
            id: id
          },
          header: {
            'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
            'content-type': 'application/json; charset=utf-8'
          }
        }
      })
    },
    remove (node, data) {
      console.log(node, data)
      // /deletemenu
      var _this = this
      _this.confirm({
        content: '确认删除这项菜单吗？',
        confirmCallback () {
          _this.deleteMenu(data.id)
        },
        cancelCallback () {}
      })
    },
    renderContent(h, { node, data, store }) {
      return (
        <span class="custom-tree-node">
          <span>{node.label}&nbsp;&nbsp;&nbsp;</span>
          <span>
            <el-button size="mini" on-click={ () => this.append(data) }>添加子菜单</el-button>
            <el-button type="danger" size="mini" on-click={ () => this.remove(node, data) }>删除菜单</el-button>
          </span>
        </span>);
    },
    getMenuData () {
      var _this = this
      _this.$get({
        type: '$get',
        obj: {
          url: _this.api.getmenu,
          success: function (res) {
            if (res.status == 200) {
              _this.alertsu('获取菜单数据成功！','获取菜单数据',2000)
              var treedata = _this.tree1(res.data, _this.menuTreeData)
              var treedata2 = _this.data2treeDG(treedata[0], treedata[1], _this.menuTreeData)
              _this.menudata = treedata2
            }
            else {
              _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
            }
          },
          error: function () {
            _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
          }
        },
        param: {
          id: 1
        }
      })
    },
    innerVisiblefn(){
      this.innerVisible = false
    },
    innerVisibleClose(){
      this.innerVisible = false
    },
    dialogFormVisiblefn(){
      this.dialogFormVisible = false
    },
    clearParend () {
      this.parend = {
        menu_name: null,
        menu_href: null,
        menu_icon: null
      }
    },
    saveMenu () {
      var _this = this
      this.$axios({
         type: '$axios',
         obj: {
          url: _this.api.createClassAMenu,
          method: 'post',
          success (res) {
            _this.dialogFormVisible = false
            if (res.data.code == 200) {
              _this.alertsu('创建一级菜单成功！')
              _this.getMenuData()
              _this.clearParend()
            } else {
              _this.alerter(res.data.msg)
            }
          },
          error () {
            _this.dialogFormVisible = false
            _this.alertsu('创建一级菜单失败！')
          },
          data: _this.parend,
          header: {
            'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
            'content-type': 'application/json; charset=utf-8'
          }
        }
      })
    },
    setCreateClassAMenu () {
      var _this = this
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          _this.saveMenu()
        }
      })
    }
  }
}
</script>
<style lang="less">
.el-select-dropdown__list {
    overflow: scroll;
}
.iconlist .el-radio{
  margin-left:30px;margin-top:14px;    display: inline-block;
    width: 60px;
}
.xzxc-create-menu .el-radio .el-radio__label{font-size:20px;}
.xzxc-create-menu .el-tree-node__content{padding-top:10px;padding-bottom:10px;}
</style>



